import { useEffect, useContext } from "react";
import { Link } from "react-router-dom";
import Repos from "../users/Repos";

import GithubContext from "../../context/github/githubContext";
import Spinner from "../layout/Spinner";

const User = ({ match }) => {
  const { user, getSingleUser, loading, getRepos, repos } =
    useContext(GithubContext);
  useEffect(() => {
    getSingleUser(match.params.login);
    getRepos(match.params.login);
    // eslint-disable-next-line
  }, []);

  const {
    hireable,
    avatar_url,
    login,
    location,
    bio,
    html_url,
    blog,
    public_repos,
    public_gists,
    followers,
    following,
  } = user;

  return (
    <div className="col-12">
      {loading ? (
        <Spinner />
      ) : (
        <>
          <Link className="btn btn-dark text-light" to="/">
            Back to Search
          </Link>
          <span className="ms-2">
            Hireable :{" "}
            {hireable ? (
              <i className="fa fa-times text-danger"></i>
            ) : (
              <i className="fa fa-check text-success"></i>
            )}
          </span>
          <div className="card mt-3">
            <div className="row">
              <div className="col-6 text-center">
                <div>
                  <img
                    style={{
                      width: "100px",
                      height: "100px",
                      borderRadius: "50%",
                      margin: "15px auto",
                      display: "block",
                    }}
                    src={avatar_url}
                    alt={login}
                  />
                  {login && <h2 className="h4">{login}</h2>}
                  {location && <h2 className="h4">{location}</h2>}
                </div>
              </div>
              <div className="col-6">
                <div className="py-3">
                  <p>
                    <strong>BIO : {bio} </strong>
                  </p>
                  <div className="my-3">
                    <a
                      className="btn btn-dark text-light"
                      href={html_url}
                      target="_blank"
                      rel="noreferrer"
                    >
                      VISIT GITHUB
                    </a>
                  </div>
                  <div>
                    <p>Login : {login}</p>
                  </div>
                  <div>
                    website :
                    <a target="_blank" href={blog} rel="noreferrer">
                      {blog}
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="card my-3">
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                padding: "30px 0",
              }}
            >
              <span className="badge bg-danger mx-2">
                Followers {followers}
              </span>
              <span className="badge bg-success mx-2">
                Following {following}
              </span>
              <span className="badge bg-warning mx-2">
                Public Repos {public_repos}
              </span>
              <span className="badge bg-info mx-2">
                Public Gists {public_gists}
              </span>
            </div>
          </div>
          <Repos repos={repos} />
        </>
      )}
    </div>
  );
};

export default User;
